import { Configuration, OpenAIApi } from 'openai';

const configuration = new Configuration({
  apiKey: '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',

  headers: { 
    'User-Agent': 'MyApp/1.0.0',
    'Authorization': 'Bearer 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'
  }
});

const openai = new OpenAIApi(configuration);

const text = document.getElementById('text-input');
const submitButton = document.getElementById('submit-button');

submitButton.addEventListener('click', async () => {
const response = await openai.createCompletion({

  model: 'text-davinci-003',
  prompt: text.value,
  temperature: 0.7,
  max_tokens: 256,
  top_p: 1,
  frequency_penalty: 0,
  presence_penalty: 0,
});

const generatedText = response.data.choices[0].text;
const formattedText = generatedText.split('\n\n').map(paragraph => `<p>${paragraph}</p>`).join('');
document.getElementById('answer').innerHTML = formattedText

});